* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Quicksand, sans-serif;
}

body {
  background-color: #c3bfb8;
}

ul {
  list-style: none;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

form:after {
  content: "";
}

.cart-container, .cartItem-container, .price-container {
  border: 1px solid #000;
}

.loginForm-container {
  text-align: left;
  background-color: #524b3e;
  padding: .2em;
}

.loginForm-inner input {
  width: 100%;
  appearance: none;
  background-color: #c3bfb8;
  outline: none;
}

.login-input {
  margin-top: 5px;
  margin-bottom: 5px;
}

.login-text-container {
  text-align: center;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr;
  align-items: center;
  row-gap: 5px;
  display: grid;
}

.login-text-container p {
  grid-area: 1 / 1 / 2 / 2;
}

.login-btn {
  grid-area: 2 / 1 / 3 / 2;
}

.reg-btn {
  grid-area: 2 / 2 / 3 / 3;
}

.error-text {
  text-align: center;
  margin-bottom: 10px;
}

#root {
  height: 100%;
  background-color: #c3bfb8;
}

.componentContainer {
  min-height: 100vh;
  grid-template-rows: 100px 100% 50px;
  grid-template-columns: 150px 1fr;
  display: grid;
}

.footer-container {
  text-align: center;
  background-color: #1d5c63;
  grid-area: 3 / 1 / 4 / 3;
}

.header-cointainer {
  color: #fff;
  background-color: #1d5c63;
  grid-area: 1 / 1 / 2 / 3;
}

.sidebar-container {
  background-color: #417d7a;
}

.sidebarItem-container {
  background-color: #254a48;
}

.products-container {
  grid-area: 2 / 2 / 3 / 3;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  place-items: center;
  row-gap: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  display: grid;
}

.card-container {
  width: 250px;
  height: 400px;
  background-color: #1d5c63;
  border-radius: .2em;
  box-shadow: 0 10px 20px #0003;
}

.productCard-content {
  text-align: center;
  background-color: #417d7a;
  margin: 1em;
  padding: 10px;
}

.productCard-img {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.product-info p {
  text-align: left;
  color: #ede6db;
  margin: auto;
  padding: 2px;
  font-size: .85em;
}

.name, .price {
  color: #1a3c40;
}

.product-info {
  margin: .6em 0;
}

.productCard-buttonContainer {
  color: #1a3c40;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5em;
  display: flex;
}

.card-button {
  cursor: pointer;
  align-items: center;
  padding: .5em;
  display: flex;
}

.card-button p {
  padding: .5em .8em;
}

.welcome {
  text-align: center;
}

